import React, {
  useCallback,
} from 'react';
import { useHistory } from 'react-router-dom';
import './index.scss';

function PayError() {
  const history = useHistory();
  const handleClick = useCallback(() => {
    history.replace('/');
  }, [history]);
  return (
    <div className="error-wrap">
      <i />
      <p>支付失败</p>
      <a href="javascript:;" onClick={handleClick}>返回首页</a>
    </div>
  );
}

export default PayError;
